import { useState } from 'react'
import isPomodoro from './custom-methods/isPomodoro'

const Header = ({ changeTimerSettings, timerInitialSettings }) => {
    const [timerTitile, setTimerTitle] = useState('Pomodoro Timer')
    const [isTimerFormActive, setIsTimerFormActive] = useState(false)
    const [workSessionsCount, setWorkSessionsCount] = useState(timerInitialSettings[0])
    const [workDuraton, setWorkDuration] = useState(timerInitialSettings[1])
    const [sBreakDuration, setSBreakDuration] = useState(timerInitialSettings[2])
    const [lBreakDuration, setLBreakDuration] = useState(timerInitialSettings[3])


    const openTimerForm = () => {
        setIsTimerFormActive(true)
        setTimerTitle('Custom Timer')
    }

    const setDefaultTimer = () => {
        changeTimerSettings(timerInitialSettings)
        setWorkSessionsCount(timerInitialSettings[0])
        setWorkDuration(timerInitialSettings[1])
        setSBreakDuration(timerInitialSettings[2])
        setLBreakDuration(timerInitialSettings[3])
        setTimerTitle('Pomodoro Timer')
    }

    const onSubmit = (e) => {
        e.preventDefault()

        if (!workSessionsCount || !workDuraton || !sBreakDuration || !lBreakDuration) {
            alert('Please, fill all fields')
            return
        }

        const newSettings = [workSessionsCount, workDuraton, sBreakDuration, lBreakDuration]

        changeTimerSettings(newSettings)

        setIsTimerFormActive(false)

        isPomodoro(newSettings, timerInitialSettings) ?
            setTimerTitle('Pomodoro Timer') :
            setTimerTitle('Custom Timer')
    }

    const onCancel = () => {
        setIsTimerFormActive(false)

        isPomodoro([workSessionsCount, workDuraton, sBreakDuration, lBreakDuration], timerInitialSettings) &&
            setTimerTitle('Pomodoro Timer')
    }

    return (
        <header className='header'>
            <h1>{timerTitile}</h1>
            {isTimerFormActive ?
                (
                    <form className='set-timer-form' onSubmit={onSubmit}>
                        <div className='set-timer-fields'>
                            <div className='field'>
                                <label>Work Sessions</label>
                                <input
                                    type='number'
                                    value={workSessionsCount}
                                    onChange={(e) => setWorkSessionsCount(parseInt(e.target.value))}
                                    min='1'
                                />
                            </div>
                            <div className='field'>
                                <label>Work Duration</label>
                                <input
                                    type='number'
                                    value={workDuraton}
                                    onChange={(e) => setWorkDuration(parseInt(e.target.value))}
                                    min='1'
                                />
                            </div>
                            <div className='field'>
                                <label>Short Break Duration</label>
                                <input
                                    type='number'
                                    value={sBreakDuration}
                                    onChange={(e) => setSBreakDuration(parseInt(e.target.value))}
                                    min='1'
                                />
                            </div>
                            <div className='field'>
                                <label>Long Break Duration</label>
                                <input
                                    type='number'
                                    value={lBreakDuration}
                                    onChange={(e) => setLBreakDuration(parseInt(e.target.value))}
                                    min='1'
                                />
                            </div>
                        </div>

                        <div>
                            <input type='submit' value='Save Timer' />
                            <input type='button' value='Cancel' onClick={onCancel} />
                        </div>
                    </form>
                ) : (
                    <div>
                        <button onClick={setDefaultTimer}>Default</button>
                        <button onClick={openTimerForm}>Set Custom Timer</button>
                    </div>
                )
            }
        </header>
    )
}

export default Header