/**
 * Help material for Timer's start/pause/reset functionalities:
 * https://blog.greenroots.info/how-to-use-javascript-scheduling-methods-with-react-hooks-ckh1jzb0u01sg2ps1cvm4afzr
 */

import { useState, useRef, useEffect, useCallback } from 'react'
import Counter from './timer-components/Counter'
import Controls from './timer-components/Controls'
import TimerCycle from './timer-components/TimerCycle'
import setInitialStages from './custom-methods/setInitialStages'
import calcStagesOffsets from './custom-methods/calcStagesOffsets'
import offsetProgressBar from './custom-methods/offsetProgressBar'

const Timer = ({ workSessions, workDuraton, shortBreakDuration, longBreakDuration, getTimerStage, trackingTaskText }) => {
    const resetTotalTime = workSessions * workDuraton +
        (workSessions - 1) * shortBreakDuration + longBreakDuration
    const totalTimeRef = useRef(resetTotalTime)
    totalTimeRef.current = resetTotalTime

    const resetStages = setInitialStages(workSessions, workDuraton, shortBreakDuration, longBreakDuration)
    const stagesRef = useRef(resetStages)
    stagesRef.current = resetStages

    const [counterCurrentState, setCounterCurrentState] = useState(resetTotalTime)
    const countRef = useRef(counterCurrentState)
    countRef.current = counterCurrentState

    const [isTimerActive, setIsTimerActive] = useState(false)
    const timerStateRef = useRef(isTimerActive)
    timerStateRef.current = isTimerActive

    const [stagesState, setStagesState] = useState(resetStages)
    const stagesStateRef = useRef(stagesState)
    stagesStateRef.current = stagesState

    const [currentStage, setCurrentStage] = useState(1)
    const stageIdRef = useRef(currentStage)
    stageIdRef.current = currentStage

    const progressBarRadius = 52
    const stageRadius = progressBarRadius / 6.5
    const circleLength = 2 * Math.PI * progressBarRadius
    const stageArc = circleLength / (stagesRef.current.length + 1)
    const progressBarLength = stageArc * stagesRef.current.length
    const stagesOffsets = calcStagesOffsets(stagesRef.current, stageArc)
    const progressBarState = -Math.round(Math.abs(progressBarLength - offsetProgressBar(stagesOffsets, stagesStateRef.current)))

    // Invoking getTimerStage function directly in useEffect causes "missing dependency" worning...  
    // Turns out, itâ€™s all down to the way Referential Equality works in JavaScript...
    // https://pauldrummond.dev/posts/useeffect-and-exhaustive-deps/
    const getTimerStageCallback = useCallback(() => {
        getTimerStage(stageIdRef.current % 2 !== 0)
    }, [getTimerStage])

    useEffect(() => {
        let intervalID

        if (isTimerActive) {
            intervalID = setInterval(() => {
                // Refresh Stage counter
                let currStageArr = stagesStateRef.current
                let id = stageIdRef.current
                let stageTime = currStageArr[id - 1]

                if (stageTime > 0) {
                    stageTime--
                } else if (id < currStageArr.length) {
                    setCurrentStage(++id)
                    stageTime = currStageArr[id - 1]
                    stageTime--
                }

                currStageArr[id - 1] = stageTime

                setStagesState(currStageArr)

                // Refresh Timer counter
                let currCounter = countRef.current
                setCounterCurrentState(--currCounter)

                // Stop timer at 0
                if (currCounter === 0) {
                    clearInterval(intervalID)
                    let currentTimerState = timerStateRef.current
                    setIsTimerActive(!currentTimerState)
                }

                getTimerStageCallback()
            }, 1000);

        } else {
            clearInterval(intervalID)
        }

        return () => clearInterval(intervalID)
    }, [isTimerActive, getTimerStageCallback])

    const resetTimer = () => {
        setCounterCurrentState(totalTimeRef.current)
        setStagesState(stagesRef.current)
        setCurrentStage(1)
        setIsTimerActive(false)
    }

    useEffect(() => {
        resetTimer()
    }, [workSessions, workDuraton, shortBreakDuration, longBreakDuration])

    const manageTimerState = () => {
        setIsTimerActive(!isTimerActive)

        if (counterCurrentState === 0) {
            setCounterCurrentState(resetTotalTime)
            setStagesState(resetStages)
            setCurrentStage(1)
        }
    }

    return (
        <div className='timer-section'>
            <div className='timer-bckgr'></div>
            <p className='tracking-task-text'>
                {trackingTaskText ? trackingTaskText : 'There isn\'t tracking task'}
            </p>
            <Counter totalTime={counterCurrentState} />
            <Controls
                reset={resetTimer}
                manageTimerState={manageTimerState}
                isTimerStarted={isTimerActive}
            />
            <TimerCycle
                stagesArray={stagesState}
                radius={progressBarRadius}
                stageRadius={stageRadius}
                progressBarLength={progressBarLength}
                offset={progressBarState}
            />
        </div>
    )
}

export default Timer