import formatTime from '../custom-methods/formatTime'

const TimerStage = ({ id, stageName, duration, position, stageRadius }) => {
    return (
        <g id={id} className={`stage-container ${duration === 0 ? 'passed-time' : ''}`} style={position}>
            <circle cx='50%' cy='50%' r={stageRadius} />
            <text className='stage-name' x='50%' y='50%' textAnchor='middle' alignmentBaseline='central' dominantBaseline='central'>
                {stageName}
            </text>
            <text className='stage-time' x='50%' y='50%' textAnchor='middle' alignmentBaseline='central' dominantBaseline='central'>
                {formatTime(duration, duration > 59 * 60)}
            </text>
        </g>
    )
}

export default TimerStage