const setInitialStages = (workSessions, workTime, shortBreak, longBreak) => {
    let stagesDurations = []
    const stagesCount = workSessions + (workSessions - 1) + 1

    for (let i = 0; i < stagesCount; i++) {
        let time

        if (i % 2 === 0) { time = workTime }
        else if (i === stagesCount - 1) { time = longBreak }
        else { time = shortBreak }

        stagesDurations.push(time)
    }

    return stagesDurations
}

export default setInitialStages