import TimerStage from './TimerStage'
import findAngleWithThreeSides from '../custom-methods/findAngleWithThreeSides'

const TimerCycle = ({ stagesArray, radius, stageRadius, progressBarLength, offset }) => {
    const stagesCount = stagesArray.length
    const stageDegrees = 360 / (stagesCount + 1)
    const progressBarPadding = 8
    const correctionAngleDeg = findAngleWithThreeSides(stageRadius, radius, radius)
    const progressBarEndX = Math.sin((stageDegrees) * (Math.PI / 180)) * radius
    const progressBarEndY = Math.cos((stageDegrees) * (Math.PI / 180)) * radius

    const createStages = () => {
        let cycle = []
        let stagesCounter = 1

        for (let i = 0; i < stagesCount; i++) {
            let name
            let time = stagesArray[i]
            const degrees = (stageDegrees * stagesCounter) + 180 - correctionAngleDeg + 1  // +1 is to hide the round stroke-linecap at the end
            const radians = degrees * (Math.PI / 180) * (-1) // -1 turn stages sequence clockwise
            const position = {
                transform:
                    `translateX(calc(${Math.sin(radians) * radius}px)) 
                    translateY(calc(${Math.cos(radians) * radius}px))`
            }

            if (i % 2 === 0) {
                name = 'Work'
            } else if (i === stagesCount - 1) {
                name = 'Long Break'
            } else {
                name = 'Break'
            }

            cycle.push(
                <TimerStage
                    key={stagesCounter.toString()}
                    id={stagesCounter}
                    stageName={name}
                    duration={time}
                    position={position}
                    stageRadius={stageRadius}
                />
            )

            stagesCounter++
        }

        return cycle
    }

    return (
        <div className='timer-cycle'>
            <svg viewBox="0 0 120 120" xmlns="http://www.w3.org/2000/svg">
                <circle
                    className="circle-passive"
                    cx="50%" cy="50%" r={radius}
                    strokeDasharray={progressBarLength}
                />
                <path
                    className="circle-active"
                    d={`
                        M${radius + progressBarPadding},${progressBarPadding} 
                        A${radius},${radius} 0 1,1 ${radius - progressBarEndX + progressBarPadding},${radius - progressBarEndY + progressBarPadding}
                    `}
                    strokeDasharray={progressBarLength}
                    strokeDashoffset={offset}
                />
                {createStages().map((item) => item)}
            </svg>
        </div>
    )
}

export default TimerCycle