import { useState } from 'react'
import Header from './components/Header'
import TodoList from './components/TodoList'
import Timer from './components/Timer'
import useTimerManager from './components/custom-hooks/useTimerManager'

function App() {
  const timerInitialSettings = [3, 15, 5, 10]
  const [timerSettings, setTimerSettings] = useTimerManager(timerInitialSettings, 'timer')
  const [workSessions, setWorkSessions] = useState(timerSettings[0])
  const [workDuraton, setWorkDuration] = useState(timerSettings[1])
  const [sBreakDuration, setSBreakDuration] = useState(timerSettings[2])
  const [lBreakDuration, setLBreakDuration] = useState(timerSettings[3])
  const [trackingTask, setTrackingTask] = useState()
  const [time, setTime] = useState()

  const changeTimerSettings = (arr) => {
    setTimerSettings(arr)
    setWorkSessions(arr[0])
    setWorkDuration(arr[1])
    setSBreakDuration(arr[2])
    setLBreakDuration(arr[3])
  }

  const setTimerStage = (isTimerInWorkSession) => {
    if (trackingTask !== undefined && isTimerInWorkSession) {
      let newTask = trackingTask
      newTask.trackedTime++
      setTrackingTask(trackingTask)
      setTime(newTask.trackedTime)
    }
  }

  const setTrackedTask = (task) => {
    setTrackingTask(task)
  }

  return (
    <div className='app'>
      <Header
        changeTimerSettings={changeTimerSettings}
        timerInitialSettings={timerInitialSettings}
      />
      <div className='container'>
        <Timer
          workSessions={workSessions}
          workDuraton={workDuraton}
          shortBreakDuration={sBreakDuration}
          longBreakDuration={lBreakDuration}
          getTimerStage={setTimerStage}
          trackingTaskText={trackingTask && trackingTask.content}
        />
        <TodoList
          getTrackedTask={setTrackedTask}
          time={time}
        />
      </div>
    </div>
  );
}

export default App