// import playIcon from '../../icons/play.svg'
// import pauseIcon from '../../icons/pause.svg'
import resetIcon from '../../icons/reset.svg'

const Controls = ({ reset, manageTimerState, isTimerStarted }) => {
    return (
        <div className='controls'>
            <div>
                <button className='icon reset-btn' onClick={reset}>
                    <img src={resetIcon} alt='Reset timer' aria-describedby='notification-label' />
                    <div role='tooltip' id='notification-label'>Reset</div>
                </button>
                {isTimerStarted ?
                    <button className='icon' onClick={manageTimerState}>
                        {/* <img src={pauseIcon} alt='Pause timer' aria-describedby='notification-label' /> */}
                        <div className='play-btn pause' aria-describedby='notification-label'></div>
                        <div role='tooltip' id='notification-label'>Pause</div>
                    </button> :
                    <button className='icon' onClick={manageTimerState}>
                        {/* <img src={playIcon} alt='Start timer' aria-describedby='notification-label' /> */}
                        <div className='play-btn' aria-describedby='notification-label'></div>
                        <div role='tooltip' id='notification-label'>Play</div>
                    </button>
                }
            </div>
        </div>
    )
}

export default Controls