import { useState, useEffect } from 'react'

// Custom hook stolen from "https://www.joshwcomeau.com/react/persisting-react-state-in-localstorage/"
const useTimerManager = (defaultValue, key) => {
    const [value, setValue] = useState(() => {
        const currentValue = sessionStorage.getItem(key)

        return currentValue !== null ? 
            JSON.parse(currentValue) : 
            defaultValue
    })

    useEffect(() => {
        sessionStorage.setItem(key, JSON.stringify(value))
    }, [key, value])

    return [value, setValue]
}

export default useTimerManager