import formatTime from '../custom-methods/formatTime'
import timerIcon from '../../icons/timer.svg'
import trackingIcon from '../../icons/timer-active.svg'
import editIcon from '../../icons/edit.svg'
import deleteIcon from '../../icons/delete.svg'

const Task = ({ task, isTracking, onChange }) => {
    const getChanges = (e) => {
        const changeTrigger = e.target.alt
        let actionCode = ''

        if (changeTrigger === '') {
            task.isCompleted = e.currentTarget.checked
            actionCode = 'complete'
        } else if (changeTrigger === 'Track task') {
            actionCode = 'start-track'
        } else if (changeTrigger === 'Stop tracking task') {
            actionCode = 'stop-track'
        } else if (changeTrigger === 'Edit task') {
            actionCode = 'edit'
        } else if (changeTrigger === 'Delete task') {
            actionCode = 'delete'
        }

        // isCurrentTaskTimerOn = isTaskTimerOn
        onChange(task, actionCode)
    }

    return (
        <div className={`task 
                        ${task.isActive ? 'active-task' : 'waiting'} 
                        ${task.isCompleted ? 'completed' : ''}`}>
            <div className='status-marker'></div>
            <div className='task-text-and-state'>
                <div className='task-content'>
                    <p>{task.content}</p>
                </div>
                <div className='task-state'>
                    <label className='complete-btn'>
                        <input
                            type='checkbox'
                            checked={task.isCompleted}
                            onChange={getChanges}
                        />
                        <span className='checkmark'></span>
                        <span className='status'>
                            {task.isCompleted ? 'Completed' :
                                (task.isActive ? 'Active' : 'Waiting')}
                        </span>
                        <span className='time'>{formatTime(task.trackedTime)}</span>
                        <span className='time-label'>Tracked time</span>
                    </label>
                </div>
            </div>
            <div className='task-buttons'>
                {isTracking ?
                    <button className='icon stop-track' onClick={getChanges}>
                        <img src={trackingIcon} alt='Stop task tracking' aria-describedby='notification-label' />
                        <div role='tooltip' id='notification-label'>Stop tracking</div>
                    </button> :
                    <button className='icon start-track' onClick={getChanges}>
                        <img src={timerIcon} alt='Track task' aria-describedby='notification-label' />
                        <div role='tooltip' id='notification-label'>Start tracking</div>
                    </button>
                }
                <button className='icon edit' onClick={getChanges}>
                    <img src={editIcon} alt='Edit task' aria-describedby='notification-label' />
                    <div role='tooltip' id='notification-label'>Edit</div>
                </button>
                <button className='icon delete' onClick={getChanges}>
                    <img src={deleteIcon} alt='Delete task' aria-describedby='notification-label' />
                    <div role='tooltip' id='notification-label'>Delete</div>
                </button>
            </div>
        </div>
    )
}

export default Task