import Task from './Task'

const Tasks = ({ tasks, isTracking, onChange }) => {
    return (
        <div className='tasks'>
            <div className='tasks-list-title'>
                <p>Tasks</p>
            </div>
            <div className='tasks-list'>
                {tasks.map((task) =>
                    <Task
                        key={task.id}
                        task={task}
                        isTracking={task.isActive ? isTracking : false}
                        onChange={onChange}
                    />
                )}
            </div>
        </div>
    )
}

export default Tasks