import { useCallback, useEffect, useRef, useState } from 'react'
import AddTask from './todolist-components/AddTask'
import EditTask from './todolist-components/EditTask'
import Tasks from './todolist-components/Tasks'
import useTasksManager from './custom-hooks/useTasksManager'

const TodoList = ({ getTrackedTask, time }) => {
    const [isAddFildAcitve, setIsAddFieldActive] = useState(true)
    const [tasks, setTasks] = useTasksManager('', 'tasks')
    const tasksRef = useRef(tasks)
    tasksRef.current = tasks
    const [currentTask, setCurrentTask] = useState()
    const currTaskRef = useRef(currentTask)
    currTaskRef.current = currentTask
    const [isCurrTaskTracking, setIsCurrTaskTracking] = useState(false)

    const addTask = (task) => {
        setTasks([...tasks, task])
    }

    const setTaskCallback = useCallback(() => {
        const newTasks = [...tasksRef.current]
        newTasks.filter(t => t.id === currTaskRef.current.id).trackedTime = 
        currTaskRef.current.trackedTime
        setTasks(newTasks)
    }, [setTasks])

    useEffect(() => {
        if(currTaskRef.current !== undefined) {
            setTaskCallback()
        }
    }, [time, setTaskCallback])
    
    // Save the changes of the edited task to the list of tasks
    const changeTask = (task) => {
        const newTasks = [...tasks]
        newTasks.filter(t => t.id === task.id).content = task.content
        setTasks(newTasks)
    }

    // Hide EditTask field after the current task change is submited or canceled
    const hideEditMode = () => {
        setIsAddFieldActive(true)
    }

    const getChangedTask = (task, actionCode) => {
        if(currTaskRef.current === undefined) {
            let lastActiveTask = tasksRef.current.filter(t => t.isActive === true)[0]
            lastActiveTask && (lastActiveTask.isActive = false)
        }

        if(currTaskRef.current !== undefined && currTaskRef.current !== task) {
            let deactivatedTask = currTaskRef.current
            deactivatedTask.isActive = false
            setCurrentTask(deactivatedTask)
        }

        task.isActive = true

        if(actionCode !== 'start-track') {
            setIsCurrTaskTracking(false)
            getTrackedTask(undefined)
        }

        if(actionCode === 'complete') {
            const newTasks = [...tasks]
            newTasks.filter(t => t.id === task.id).isCompleted = task.isCompleted
            setTasks(newTasks)
        } else if(actionCode === 'start-track') {
            setIsCurrTaskTracking(true)
            getTrackedTask(task)
        // } else if(actionCode === 'stop-track') {
        //     setIsCurrTaskTracking(false)
        //     getTrackedTask(undefined)
        } else if(actionCode === 'edit') {
            setIsAddFieldActive(false)
            getTrackedTask(undefined)
        } else if(actionCode === 'delete') {
            setTasks(tasks.filter((t) => t.id !== task.id))
        }

        setCurrentTask(task)
    }

    return (
        <div className='todolist-section'>
            <div className='todolist'>
                <div className='section-title'>
                    <h2>To-do List</h2>
                    <p>Set goals to track with the timer</p>
                </div>

                {isAddFildAcitve ? 
                    (<AddTask onAdd={addTask} />) : 
                    (<EditTask currentTask={currentTask} onEdit={changeTask} onCancel={hideEditMode} />)}

                <div className={isAddFildAcitve ? '' : 'disabled'}>
                    {tasks.length ?
                        (<Tasks 
                            tasks={tasks}
                            isTracking={isCurrTaskTracking}
                            onChange={getChangedTask} />) :
                        (<p>There's no items in the To-do List yet.</p>)}
                </div>
            </div>
        </div>
    )
}

export default TodoList