import { useState } from 'react'

const EditTask = ({ currentTask, onEdit, onCancel }) => {
    const [text, setText] = useState(currentTask.content)

    const submitChanges = (e) => {
        e.preventDefault()

        const editedTask = currentTask
        editedTask.content = text

        onEdit(editedTask)
        onCancel()
    }

    return (
        <form className='edit-form' onSubmit={submitChanges}>
            <label>Edit Task</label>
            <input
                type="text"
                value={text}
                onChange={(e) => { setText(e.target.value) }}
            />
            <input type="submit" value='Save Changes' />
            <input
                type="button"
                value='Cancel'
                onClick={() => { onCancel() }}
            />
        </form>
    )
}

export default EditTask