const formatTime = (seconds, isMoreThanHour) => {
    let currentHours = 0
    
    if(isMoreThanHour) {
        currentHours = Math.floor(seconds / (60 * 60))
    }
    const currentMin = Math.floor((seconds % (60 * 60)) / 60)
    const currentSec = Math.floor((seconds % (60 * 60)) % 60)

    return (
        (isMoreThanHour ?
            ((currentHours > 9 ? 
                currentHours.toString() : 
                '0' + currentHours.toString()) + ':') :
            '') +
        (currentMin > 9 ? currentMin.toString() : '0' + currentMin.toString()) + ':' +
        (currentSec > 9 ? currentSec.toString() : '0' + currentSec.toString())
    )
}

export default formatTime